import actions from '../constants/index';
import { createSlice } from '@reduxjs/toolkit'

export const twadSlice = createSlice({
  name: 'twad',
  initialState: {
    userName: '',
    userId: null,
    roleId:'',
    isPassword:"",
    userPassword: "",
    employeeName: "",
    office: {
      regionId: "",
      regionName: "",
      circleId: null,
      circleName: "",
      officeId: "",
      officeName: "",
      officeLevelId: "",
      
    },
    districtCode:""
  },
  reducers: {
    login: (state, msg) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      console.log(msg.payload, "redux")
      state.userName = msg.payload.userName
      state.userId = msg.payload.userId
      state.roleId = msg.payload.roleId
      state.isPassword = msg.payload.isPasswordChanged
      state.userPassword = msg.payload.userPassword
      state.employeeName = msg.payload.employeeName
      state.office.regionId = msg.payload.regionId
      state.office.circleId = msg.payload.circleId
      state.office.circleName = msg.payload.circleName
      state.office.officeId = msg.payload.officeId
      state.office.officeName = msg.payload.officeName
      state.office.officeLevelId = msg.payload.officeLevelId
     




    },



    decrement: (state) => {
      state.value -= 1
    },
    incrementByAmount: (state, action) => {
      state.value += action.payload
    },
    setSidebar: (state) => {
      state.sidebarShow = !state.sidebarShow
    },
    dashboard: (state, msg) => {
      state.dash.pending = msg.payload.pending
      //state.studentKey = msg.payload.studentKey
    },

    mapMeter:(state,msg)=>{
      console.log(msg.payload,"mapmeter");
      state.districtCode = msg.payload.districtCode
    }
  },
})

// Action creators are generated for each case reducer function
export const { login, decrement, incrementByAmount, setSidebar, dashboard ,mapMeter} = twadSlice.actions

export default twadSlice.reducer